import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/views/Home.vue';
import GanDengYan from '@/views/GanDengYan.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/home',
      component: Home,
    }, {
      path: '/gandengyan',
      component: GanDengYan,
    },
  ],
});
